    text-align: center;
    box-shadow: 7px 7px 5px rgba(0,0,0,0.6); 
    text-shadow: 7px 7px 5px rgba(0,0,0,0.6); 

    background: url(/src/img/life.png);
    background-position: center;
    background-size: auto 300%;
