.PanelHeader__in {
    background-color: #567599 !important;
    background: -webkit-linear-gradient(90deg, #567599,#1f2e4b);
    background: linear-gradient(90deg, #567599,#1f2e4b);
    border-bottom: 1px solid #404040;
  }

  .Panel__in {
    background: rgba(0, 0, 0, 0)!important;
  }

  .Panel::after {
    position: fixed!important;
    background-color: #567599!important;
    background: -webkit-linear-gradient(120deg, #567599,#1f2e4b);
    background: linear-gradient(100deg, #567599,#1f2e4b);
    background-position: center!important;
    background-repeat: no-repeat!important;
    background-size: 100%!important;
    filter: blur(3px);
    filter: brightness(60%);
  }

.vkuiFormField__border {
  background-color: #567599 !important;
  opacity: 0.9;
  }

  .vkuiCustomSelect__options {
    background-color: #567599 !important;
    opacity: 0.9;
  }